import React, { Component } from "react";


let residential_address_details = {
        policy_number : "",
        stand : "",
        street_name : "",
        town_city : "",
        province : "",
        country : "",
        postal_code : ""

};

let postal_address_details= {
    policy_number : "",
    box : "",
    town_city : "",
    province: "",
    country : "",
    postal_code : ""
};

let contact_details_detail = {
    policy_number : "",
    cell : "",
    tel : "",
    fax : "",
    email : "",
    website : "",
};

let policy_holder_details = {
    policy_number : "",
    title : "",
    surname : "",
    full_names : "",
    id_number : "",
    date_of_birth : "",
    nationality : "",

};

let banking_details = {
    policy_number : "",
    account_holder : "",
    account_number : "",
    bank : "",
    branch_code : "",
    branch_name : "",

};


class PostalAddress extends  Component{
    constructor(props){
        super(props);
        this.state = {
            postal_address : {...postal_address_details},
            form_response : ""
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
    };

    onChangeHandler(e){
        let postal_address = Object.assign({}, this.state.postal_address);
        switch (e.target.name) {
            case 'box' : postal_address.box = e.target.value;break;
            case 'town_city' : postal_address.town_city = e.target.value;break;
            case 'province' : postal_address.province = e.target.value;break;
            case 'country' : postal_address.country = e.target.value;break;
            case 'postal_code' : postal_address.postal_code = e.target.value;break;
            default: break;
        };
        this.setState({
            postal_address : postal_address
        });

    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className={'fa fa-envelope'}> </i> Postal Address </strong></h3>
                    <div className={'row'}>

                    </div>
                </div>

                <form className={'form-horizontal'}>
                    <div className={'form-group'}>
                        <label>Box</label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'box'} value={this.state.postal_address.box} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label>Town/City</label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'town_city'} value={this.state.postal_address.town_city} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label> Province </label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'province'} value={this.state.postal_address.province} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label> Country </label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'country'} value={this.state.postal_address.country} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <label> Postal Code</label>
                        <div className={'input-group'}>
                            <input type={'text'} className={'form-control'} name={'postal_code'} value={this.state.postal_address.postal_code} onChange={e => this.onChangeHandler(e)} />
                        </div>
                    </div>

                    <div className={'form-group'}>
                        <button type={'button'} className={'btn btn-success btn-block'}><strong> <i className={'fa fa-save'}> </i> Save Postal Address </strong> </button>
                    </div>

                </form>


            </div>
        )
    }
}


